.button-controls {
  display: flex;
  align-items: center;
  font-size: 12px;
  line-height: 14px;
  font-weight: 700;
  border: none;
  outline: none;
  padding: 7px 5px;
  color: #000000;
  cursor: pointer;
  background-color: transparent;
  transition: opacity 0.3s ease; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .button-controls:hover .button-arrow__icon {
      opacity: 0.7; } }
  .button-controls__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 19px;
    height: 19px;
    margin-left: 7px;
    background-color: #000000;
    border-radius: 50%;
    color: #ffffff; }
  .button-controls__icon svg {
    transition: transform 0.3s ease; }
  .button-controls__text--close {
    display: none; }
  .button-controls.is-open .button-controls__text--open {
    display: none; }
  .button-controls.is-open .button-controls__text--close {
    display: flex; }
  .button-controls.is-open .button-controls__icon svg {
    transform: rotate(180deg); }

